<template>
  <div class="page">
    <div class="tools-bar">
      <div class="left-wrap">
        <el-input prefix-icon="el-icon-search" v-model="params.articleInfoTitle" placeholder="请输入搜索关键字" size="small"></el-input>
        <!-- <picker v-model="pickerVal" size="small" :span="-1" :column="2" :isXuanke="false"></picker> -->
        <el-col :span='11'>
          <el-select v-model="params.specialTopicInfoId" size="small" placeholder="请选择专题" @change="pickerChange">
            <el-option :label="ceil.title" :value="ceil.id" v-for="(ceil,$index) in specialData" :key="$index"></el-option>
          </el-select>
        </el-col>
        <el-button size="small" type="primary" @click="search">查询</el-button>
        <el-button size="small" type="warning" @click="reset">重置</el-button>
      </div>
      <listHandle :checkData="checkData" :module="params.moduleId" @refresh="refresh" :showDelete="false">
        <el-button type="danger" size="small" @click="handleDelete" :disabled="checkData.length<1">删除</el-button>
      </listHandle>
    </div>
    <el-table ref="multipleTable" header-cell-class-name="tableHeader" :data="tableData" v-loading="loading" element-loading-text="拼命加载中" border stripe @selection-change="handleSelectionChange">
      <el-table-column type="selection" width="55" align="center" label-class-name="checkLabel">
      </el-table-column>
      <el-table-column prop="title" label="标题" align="center" min-width="180" show-overflow-tooltip>
      </el-table-column>
      <el-table-column prop="specialTopicInfoTitle" label="所属专题" width="120" align="center">
      </el-table-column>
      <el-table-column prop="articleInfoLabelName" label="文章标签" width="120" align="center">
      </el-table-column>
      <el-table-column label="状态" width="90" align="center">
        <template slot-scope="scope">
          <el-tag size="small" :type="scope.row.status | publicStatus('style')">{{ scope.row.status | publicStatus }}</el-tag>
        </template>
      </el-table-column>
      <el-table-column prop="readNumber" label="阅读量" width="80" align="center">
      </el-table-column>
      <el-table-column prop="commentNumber" label="评论量" width="80" align="center">
      </el-table-column>
      <el-table-column prop="recommendNumber" label="推荐量" width="80" align="center">
      </el-table-column>
      <el-table-column prop="playNumber" label="播放量" width="80" align="center">
      </el-table-column>
      <el-table-column prop="collectionNumber" label="收藏数" width="80" align="center">
      </el-table-column>
      <el-table-column prop="jumpOutNumber" label="跳出率" width="100" align="center">
      </el-table-column>
      <el-table-column prop="createUse" label="操作人" width="120" align="center">
      </el-table-column>
      <el-table-column label="创建时间" width="170" align="center">
        <template slot-scope="scope">{{ scope.row.createTime | dateTime('yyyy-MM-dd hh:mm:ss') }}</template>
      </el-table-column>
      <el-table-column label="发布时间" width="170" align="center">
        <template slot-scope="scope">{{ scope.row.releaseTime | dateTime('yyyy-MM-dd hh:mm:ss') }}</template>
      </el-table-column>
      <el-table-column label="操作" width="80" align="center">
        <template slot-scope="scope ">
          <el-button type="text " size="mini" v-if="scope.row.status===2||scope.row.status===3">
            <router-link :to="{name:'zhiyuan/article',params:{type:'edit'},query:{id:scope.row.id}}" tag="span">编辑</router-link>
          </el-button>
          <el-button type="text" size="mini" v-if="scope.row.status===1" @click.stop="openSendDialog(scope.row.id)">推送</el-button>
        </template>
      </el-table-column>
    </el-table>
    <pagination ref="pageInfo" :total="pageInfo.totalElements" :page.sync="pageInfo.currentPage" @pagination="pagination"></pagination>
    <drawer class="drawer-components" v-model="visible" title="推送" width="60" right="120" placement="right" @bodyClick="handleBodyClick" showFooter>
      <el-form :model="form" inline-message ref="form" :rules="sendRules" label-suffix=":" label-width="100px">
        <el-form-item label='被推送的标题'>
          <label v-for="(item,index) in checkData" :key="index">{{item.title}}</label>
        </el-form-item>
        <el-form-item label="推送时间" prop="sendTime">
          <el-radio v-model="form.radio" label="1">直接推送</el-radio>
          <br />
          <el-radio v-model="form.radio" label="2">定时推送</el-radio>
          <el-date-picker v-model="form.sendTime" type="datetime" size="small" :picker-options="pickerOptions2" value-format="timestamp" placeholder="请选择发布时间">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="推送范围" prop="sendRange">
          <el-checkbox :indeterminate="form.isIndeterminate" v-model="checkAll" @change="handleCheckAllChange">全国</el-checkbox>
          <el-checkbox-group v-model="form.provincesIds" @change="handleCheckedCitiesChange">
            <el-checkbox v-for="(item,index) in provincesData" :key="index" :label="item.name" :disabled="item.disabled" :min="1">{{item.name}}</el-checkbox>
          </el-checkbox-group>
        </el-form-item>
      </el-form>
      <template slot="footer">
        <el-button @click="cancel" size="small">取消</el-button>
        <el-button type="primary" size="small" @click="sendFn('send')">确定</el-button>
      </template>
    </drawer>
  </div>
</template>
<script>
import { api, confirm } from '@/utils'
import { picker, listHandle, pagination, drawer } from '@/components'
export default {
  data () {
    let validateTime = (rule, value, callback) => {
      if (this.form.radio === '2' && !this.form.sendTime) {
        callback(new Error('请选择定时推送时间'))
      } else {
        callback()
      }
    }
    let validateRange = (rule, value, callback) => {
      if (this.form.provincesIds.length === 0) {
        callback(new Error('请至少选择一个推送范围'))
      } else {
        callback()
      }
    }
    return {
      params: {
        articleInfoTitle: null,
        moduleId: '',
        specialTopicInfoId: null,
        page: 1,
        sortType: 1
      },
      checkData: [],
      pickerVal: [],
      pageInfo: {
        totalElements: 0,
        currentPage: 1
      },
      tableData: [],
      loading: false,
      sendDialog: false,
      sendTitle: [],
      checkAll: false,
      form: {
        radio: '1',
        sendTime: '',
        isIndeterminate: true,
        provincesIds: []
      },
      sendRules: {
        sendTime: [{ required: true, message: '请选择推送方式', trigger: 'change', validator: validateTime }],
        sendRange: [{ type: 'array', required: true, message: '请至少选择一个推送范围', trigger: 'change', validator: validateRange }]
      },
      pickerOptions2: {
        disabledDate (time) {
          return time.getTime() < Date.now() - 8.64e7
        }
      },
      provincesData: [],
      povincesDataById: [],
      bannerId: '',
      specialData: [],
      visible: false
    }
  },
  components: {
    picker,
    listHandle,
    pagination,
    drawer
  },
  watch: {
    '$route' (to, from) {
      console.log(to.params.type)
      this.params.page = 1
      this.params.specialTopicInfoId = null
      this.params.articleInfoTitle = null
      this.type = to.params.type
      this.getArticleMajor()
      this.getData(this.params)
    }
  },
  methods: {
    pickerChange (e) {
      this.params.specialTopicInfoId = e
      this.getData(this.params)
      console.log(e)
    },

    handleDelete () {
      let txt = '删除'
      this.checkIds = []
      confirm(`您确定将选择的文章${txt}吗？`, '提示').then(() => {
        for (let item = 0; item < this.checkData.length; item++) {
          if (this.checkData[item].status === 1) {
            this.$message.error(`已发布的内容不能${txt}`)
            this.checkIds = []
            return
          } else {
            this.checkIds.push(this.checkData[item].id)
          }
        }
        if (this.checkIds.length > 0) {
          this.$fly.post(api['zhiyuan/articleDelete'], this.checkIds).then(data => {
            if (!data.data) {
              return this.$message({
                message: `${data.message}`,
                duration: 2000,
                type: 'error'
              })
            } else {
              this.$message({
                message: `${txt}成功`,
                duration: 2000,
                type: 'success'
              })
              this.refresh()
            }
          }).catch(() => {
            this.$message({
              message: `${txt}失败`,
              duration: 2000,
              type: 'error'
            })
          })
        }
      })
    },
    handleSelectionChange (e) {
      this.checkData = []
      this.lodash.map(e, (item) => {
        this.checkData.push({ id: item.id, status: item.status })
      })
    },
    // 分页
    pagination (e) {
      this.params.page = e.page
      this.params.size = e.limit
      this.getData(this.params)
    },
    reset () {
      this.params = {
        title: null,
        page: 1,
        size: this.$refs.pageInfo.pageSizes[0],
        sortType: 1
      }
      this.pickerVal = []
      this.getData(this.params)
    },
    search () {
      console.log(this.pickerVal[0])
      this.params.page = 1
      // this.params.specialTopicInfoId = this.pickerVal[0]
      // this.params.articleInfoTitle =
      this.getData(this.params)
    },
    refresh () {
      this.params.page = 1
      this.getData(this.params)
    },
    getData (params) {
      params.moduleId = this.moduleId(this.type)
      params.page--
      // console.log(this.pickerVal[0])
      // params.productId = this.pickerVal[0]
      // params.specialTopicId = this.pickerVal[1]
      this.loading = true
      this.$fly.get(api.queryArticleInfoByCondition, params).then(data => {
        setTimeout(() => {
          this.loading = false
        }, 1000)
        if (data.status === 100000) {
          let { content, totalElements } = data.data
          // console.log(content)
          this.tableData = content
          this.pageInfo = {
            totalElements: parseInt(totalElements),
            currentPage: params.page + 1
          }
        } else {
          this.$message({
            message: '请求失败',
            type: 'error'
          })
        }
      })
    },
    openSendDialog (id) {
      this.visible = true
      this.sendDialog = true
      this.bannerId = id
      this.form = {
        radio: '1',
        sendTime: '',
        isIndeterminate: true,
        provincesIds: []
      }
      this.$refs['form'].resetFields()
    },
    // 全选
    handleCheckAllChange (val) {
      let list = []
      if (val) {
        this.provincesData.map((item) => {
          if (!item.disabled) {
            return list.push(item.name)
          }
        })
      } else {
        list = []
      }
      this.form.provincesIds = list
      if (this.form.provincesIds.length === this.provincesData.length) {
        this.form.isIndeterminate = false
      }
    },
    // 反选
    handleCheckedCitiesChange (value) {
      let checkedCount = value.length
      this.checkAll = checkedCount === this.provincesData.length
      this.form.isIndeterminate = checkedCount > 0 && checkedCount < this.provincesData.length
    },
    // 取消
    cancel () {
      this.$refs['form'].resetFields()
      this.visible = false
    },
    // 推送
    sendFn (type) {
      this.$refs['form'].validate((valid) => {
        if (valid) {
          let params, url, area
          let list = []
          let provincesIds = this.form.provincesIds
          let provincesData = this.provincesData
          if (process.env.NODE_ENV === 'production') {
            for (let x in provincesData) {
              provincesIds.map((item) => {
                if (provincesData[x].name === item) {
                  list.push('pro_' + provincesData[x].code)
                }
                return list
              })
            }
          } else {
            for (let x in provincesData) {
              provincesIds.map((item) => {
                if (provincesData[x].name === item) {
                  list.push('dev_' + provincesData[x].code)
                }
                return list
              })
            }
          }
          if (!this.checkAll) {
            area = 1
          } else {
            area = 0
          }
          params = {
            id: this.bannerId,
            type: 2,
            provinceIds: `${list}`,
            area: area
          }
          if (this.form.radio === '1') {
            url = api[`bannerSend`]
          } else if (this.form.radio === '2') {
            url = api[`bannerSchedule`]
            params.date = this.form.sendTime
          }
          confirm(`您确定将推送选择的文章吗？`, '提示').then(() => {
            this.$fly.post(url, params).then(data => {
              this.$message({
                message: `推送成功`,
                duration: 2000,
                type: 'success'
              })
              this.params.page = 1
              this.getData(this.params)
            }).catch(() => {
              this.$message({
                message: `推送失败`,
                duration: 2000,
                type: 'error'
              })
              this.visible = false
            })
          }).catch(() => {
            this.visible = false
          })
        } else {
          return false
        }
      })
    },
    handleBodyClick (e) {
      let _this = this
      _this.visible = e
    },
    getArticleMajor () {
      this.$fly.get(api.queryArticleInfoCondition, { moduleId: this.moduleId(this.type) }).then(data => {
        this.specialData = data.specialTopicInfoBean
      })
    }
  },
  created () {
    this.type = this.$route.params.type
    this.getData(this.params)
    this.getArticleMajor()
    this.$fly.get(api.getProvinces).then(data => {
      if (data.status === 100000) {
        const provinces = data.data
        this.provincesData = provinces
        this.$fly.get(api.getProvinceIds).then(data1 => {
          this.povincesDataById = data1
          if (process.env.NODE_ENV === 'production') {
            provinces.map(item => {
              item['disabled'] = true
              for (let i = 0; i < data1.length; i++) {
                if (data1[i] === 'pro_' + item.code) {
                  item['disabled'] = false
                  break
                }
              }
            })
          } else {
            provinces.map(item => {
              item['disabled'] = true
              for (let i = 0; i < data1.length; i++) {
                if (data1[i] === 'dev_' + item.code) {
                  item['disabled'] = false
                  break
                }
              }
            })
          }
          this.$set(this, 'provincesData', provinces)
        })
      } else {
        this.$message({
          message: '请求失败',
          type: 'error'
        })
      }
    })
  },
  mounted () {
    let _this = this
    _this.params.size = _this.$refs.pageInfo.pageSizes[0]
  }
}
</script>



